const css = 'cognition.css'

const debounce = (callback, wait = 20) => {
	let timerId
	
	return async (...args) => {
		//console.error(args, "debounce", arguments)
        clearTimeout(timerId)
        timerId = setTimeout(() =>{ 
          callback(...args)
            return timerId
        }, wait)
	}
}


