export const formatName = name => name.toLowerCase().replace( / /g,"_")

// This just gets the data from wiki and presents it on screen
// To look things up on wiki we neeed to borrow their API...
export const lookUp = async (page) =>{
    return await fetch(`./data/${formatName(page)}-clean.json`)
}


