class Biases extends HTMLElement {

    constructor() {

      super()

      const domain = this.attachShadow({ 'mode': 'open' });

      // stick the template onto the page
      this.dom = domain.appendChild(template.content.cloneNode(true))
      
      this.root = template // domain.querySelector('.human-biases')
      this.svg = domain.querySelector('svg')
      this.app = domain.querySelector('.human-biases')
      this.buttonReadMore = domain.querySelector('#read-more')
      
      // we either want to use CSS for the text or SVG but not both
      // SVG text is cool as it always scales proportionatly where as
      // CSS text always stays the same size and can be controlled better
      this.header = domain.querySelector('header')
      this.information = domain.getElementById('learn-more')
      this.errors = domain.getElementById('errors')
    
      const doms = domain.getElementById('biases')
        
        const selects = domain.querySelectorAll('select')
        for (let s in selects)
        {
            if (selects.hasOwnProperty(s))
            {
                const select = selects[s]
               // console.error(select,"select", select.selectedIndex, select.options, select.options[select.selectedIndex])
                //select.onselect = event => {}

                const onSelect = event=>{
                    const zoom = parseFloat(event.target.value)
                    this.app.style.setProperty('--size', zoom)
                    //console.log( "select", zoom, select.selectedIndex , select.options)
                }
                
                select.addEventListener("select",onSelect )
                select.addEventListener("change",onSelect )      
            }
        }
        
      const buttons = domain.querySelectorAll('button')
      for (let b in buttons)
      {
          if (buttons.hasOwnProperty(b))
          {
            const button = buttons[b]
            button.onclick = event => {
                
                const value = parseFloat(button.getAttribute("data-value"))
                
                switch(button.getAttribute("data-var"))
                {
                    case "size":
                        const zoom = parseFloat( getComputedStyle(this.svg,null).getPropertyValue('--size') )
                        this.app.style.setProperty('--size', zoom + value )
                        //console.log("Changing zoom by",{zoom, value})
                        break

                    case "rotation":
                        //const rotation = parseFloat( getComputedStyle(this.svg,null).getPropertyValue('--rotation') )
                        //this.app.style.setProperty('--rotation', value )
                        this.infographic.revolve(value)
                        break
                }
                event.preventDefault()
            }
          }
           
      } 

      //console.error( "this", this, {buttons, domain, svg:this.svg } )
    }

    // LifeCycle
    connectedCallback() {

        const trigger = () => {
            // pause
            setTimeout(() => {
                
                this.initialise()

            }, 1)
        }

        if ( document.readyState !== 'complete' )
        {
            window.addEventListener('load', (event) => {
                trigger()
            })

        }else{
            trigger()
        }
        
      

        // now draw the thing!
        //this.render()
    }

    // disconnectedCallback: Invoked each time the custom element is disconnected from the document's DOM.
    // adoptedCallback: Invoked each time the custom element is moved to a new document.
    // attributeChangedCallback: Invoked each time one of the custom element's attributes is added, removed, or changed. Which attributes to notice change for is specified in a static get observedAttributes method
    attributeChangedCallback(attributes){

        // OK, so the outer page has changed it's markup and one of the 
        // attributes has changed. 
        // Here we update which node to start from set by parent
        // If it updates here, we need to tell the D thing to update to a node
        console.error("attributeChangedCallback",attributes)
        
        // now draw the thing!
        this.render()
    }

    dispatchEvent(name, detail){
        //this.root
        this.shadowRoot.dispatchEvent(new CustomEvent(name, {
            bubbles: true,
            // Events only cross shadow DOM boundaries if their composed flag is set to true.
            composed: true,
            detail: detail
        }))
    }

    initialise(){

        const svgTextMode = this.hasAttribute('text')
        
        // // general spacing
        // padding : 30,
        // bubbleRadius : 8,
        // // Realtime options
        // // Pacman mouth size
        // mouth:30,
        // // timings
        // duration : {
        //     lines:15,
        //     circles:500,
        //     nodes:400
        // },
        // delay : {
        //     lines:5,
        //     circles:30,
        //     nodes:50
        // },
        // textBoxWidth : 60,
        // textBoxHeight : 200

        const options = {
            // Angle of the entry point 265
            angle : 265
        }

        // hide / show the SVG titles...
        if (!svgTextMode)
        {
            // doesnt scale heading text
            this.root.classList.add("text-css")
        } else{
            this.root.classList.add("text-svg")
        }

        // if an attribute was set on the custom attribute...
        if (this.hasAttribute('show-controls'))
        {
            // allow zoom to be shown...
        } 

        if (this.hasAttribute('show-numbers'))
        {
            options.showNumbers = !!this.hasAttribute('show-numbers')
        } 

        if (this.hasAttribute('zoom'))
        {
            // allow zoom to be shown
            // set zoom (zoom is entirely this component!)
            this.svg.style.setProperty( '--size', parseFloat(this.getAttribute('zoom')) )
        } 

        const findFieldAndAdd = (attribute, fallback) => {
            if (this.hasAttribute(attribute))
            {
                options[attribute] = parseFloat( this.getAttribute(attribute) )
            }else if (fallback){
                options[attribute] = fallback
            }
        }
           
        findFieldAndAdd('padding')
        findFieldAndAdd('mouth')
        findFieldAndAdd('angle')
        findFieldAndAdd('saturation')
        findFieldAndAdd('lightness')

        // set node level (less than 3 requires extra conf)
        const level = this.hasAttribute('level') ? this.hasAttribute('level') : 3
       
        // some dom elements... check to see if the element has not been outslotted??
        const h1 = this.header.querySelector('h3')
        const h2 = this.header.querySelector('h4')

        // initialise
        this.infographic = registerBiases( this.svg, "data/cognition.json", level, options )
        //this.infographic = window.BIASES( this.svg, "data/cognition.json", level, options )


        const loadTrait = async(trait) => {
            // const description = item.parent.data.name
            // const depth = item.depth

            //if (type === 'click')
            try {
                const wikiResponse = await lookUp( trait )
                //console.error("WIKI",wikiResponse)
                
                if (wikiResponse.status !== 200) 
                {
                    throw Error('Looks like there was a problem. Status Code: ' + wikiResponse.status)
                }
            
                // Examine the text in the response
                const data = await wikiResponse.json()
                //const data = json.parse
            
                const title = data.title 
                const html = data.text 
                
                // anchors wont work...
                this.information.innerHTML = `${html}<a href="#bias">Back to the top</a>`

               // console.log( title, {html, data})
               this.information.scrollIntoView()

                // why you no worky?
                this.dispatchEvent(EVENT_TRAIT, {data, trait} )
            
            } catch (error) {
                console.log("Wiki fetch failed", error)
            }
            
            //console.log("onTrait", {trait, description, depth, o} )
        }

        this.buttonReadMore.addEventListener("mousedown", async (event) => {
            
            const trait = event.target.getAttribute("data-link")
            console.log(trait, event, this.buttonReadMore)
            console.log("BEFORE", Date.now() )
            await loadTrait(trait)
            // focus?
            console.log("AFTER", Date.now() )
          
        }, false)

        this.infographic.loadPromise.then( result => {
            
            //loaded json successfully and app is now active!
           // console.error("loadPromise resolved!", result)

            const debouncedTraits = debounce( loadTrait, 1000 )
                
            // now we can access some of the callbacks...
            this.infographic.setTraitCallback( async (o) => {
               // await loadTrait(o)
               const item = o.item
               const trait = item.data.name
               const description = item.parent.data.name

               // update button link for clicking and loading
               this.buttonReadMore.setAttribute("data-link", trait)
               
               // if we are in CSS text mode... update the DOM
               if (!svgTextMode)
               {
                   h1.innerHTML = description
                   h2.innerHTML = trait
               }
                //debouncedTraits(trait)
            })
            
            this.infographic.setDescriptionCallback( o => {
                const {type, item} = o
                const description = item.data.name
                const depth = item.depth
                if (!svgTextMode)
                {
                    h1.innerHTML = description
                    //h2.innerHTML = trait
                }
                //console.log("onDescription", {item, description, depth })
                this.dispatchEvent(EVENT_DESCRIPTION,o)
            })

            // Completed load (return options set from attributes)
            this.dispatchEvent(EVENT_AVAILABLE, options)

        }).catch( error =>{ 

            console.error("Cannot continue", error) 
            // display error message...
            this.errors.classList.toggle( "visible", true )
            this.errors.innerHTML = `Cannot continue as the data file couldn't be found<br>${error}`
        })

    }

    render(){
        
        // if an attribute was set on the custom attribute...
        if (this.hasAttribute('show-controls'))
        {
            // allow zoom to be shown...
        } 
        if (this.hasAttribute('zoom'))
        {
            // allow zoom to be shown...
        } 
        if (this.hasAttribute('rotation'))
        {
            // allow zoom to be shown...

        } 
        if (this.hasAttribute('level'))
        {
            // set node level (less than 3 requires extra conf)
            // set config directly onto graphic...
            this.infographic.level = this.getAttribute('level')
        } 
   
        // svg = .innerHTML
        // canvas = new Canvas(sizes from svg?)
        // pasteSVG( svg, canvas ).then( img => { show on screen! } )
    }

  }


   
  window.customElements.define('human-biases', Biases)