/* 

You can use this independently of the WebComponent

*/

import { json, select, tree, hsl, event, hierarchy} from "d3"
// import { textwrap } from 'd3-textwrap'
import { textwrap } from './d3.wrap'

// register D3 object
const d3 = {}
d3.select = select
d3.event = event
d3.hierarchy = hierarchy

// Shortcuts that minimimise nicely
const cos = Math.cos
const sin = Math.sin
const pi = Math.PI
const tau = pi * 2

const selectAndCopyText = text => {
    if ( navigator.clipboard && text && text.length )
    {
        return navigator.clipboard.writeText(text)
    }else{
        // do the exec sillyness?
        return Promise.reject("No")
    }
}

/* 
returns a SVG path element that represent a ellipse.
URL: SVG Circle Arc http://xahlee.info/js/svg_circle_arc.html
Version 2019-06-19
*/
const f_matrix_times = (( [[a,b], [c,d]], [x,y]) => [ a * x + b * y, c * x + d * y])
const f_rotate_matrix = ((x) => [[cos(x),-sin(x)], [sin(x), cos(x)]])
const f_vec_add = (([a1, a2], [b1, b2]) => [a1 + b1, a2 + b2])

// startAngle - start angle in radians
//   angle to sweep, in radian. positive
const drawRingPath = ( radius = 10, startAngle=0, arcAngle=pi, rotate=0, [cx,cy] = [0,0] ) =>{
    // constrain to 2
    arcAngle = arcAngle % tau
    const rotMatrix = f_rotate_matrix (rotate)
    const [sX, sY] = ( f_vec_add ( f_matrix_times ( rotMatrix, [radius * cos(startAngle), radius * sin(startAngle)] ), [cx,cy] ) )
    const [eX, eY] = ( f_vec_add ( f_matrix_times ( rotMatrix, [radius * cos(startAngle+arcAngle), radius * sin(startAngle+arcAngle)] ), [cx,cy] ) )
    const fA = ( (  arcAngle > pi ) ? 1 : 0 )
    const fS = ( (  arcAngle > 0 ) ? 1 : 0 )
    return `M ${sX} ${sY} A ` + [ radius , radius , rotate / tau * 360, fA, fS, eX, eY ].join(" ")
}

const isDarkMode = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches


// VISUALISER ========================

const defaultOptions = {
    // general spacing
    padding : 30,
    bubbleRadius : 8,
    // Realtime options
    showNumbers : false,
    // Pacman mouth size
    mouth:30,
    // Angle of the entry point
    angle : 0, //265,

    //colours HSL
    saturation:isDarkMode ? 0.8 : 0.3,
    lightness:isDarkMode ? 0.5 : 0.4,

    // timings
    duration : {
        lines:15,
        circles:500,
        nodes:400
    },
    delay : {
        lines:5,
        circles:30,
        nodes:50
    },

    // outer edge
    textBoxWidth : 74,
    textBoxHeight : 200,

    radiuses:{
        data:0.54,
        // description distance from centre
        title:0.47,
        // coloured ring
        ring: 0.85
    }
}

// Actual export
const bias = ( 
    host="svg", 
    dataFile = "cognition.json", 
    level=3,
    options = defaultOptions
) => {


    // before we do *anything* let us check to see we have the
    // correct dependencies available to hand...
    // 1. D3
    if (!d3)
    {
        console.error( "D3 is NOT available" )
        return { error:"Dependencies not met [d3]" }
    }


    // 2. D3.text-wrap
    if (!textwrap)
    {
        console.error("D3.textwrap is NOT available", d3 )
        return { error:"Dependencies not met [d3-textwrap]" }
    }


    // overwritable via externally accessible methods
    let onTrait = e => console.error("ontraits", e)
    let onDescription = e => console.error("onDescription",e )

    // if no options are supplied, it uses defaultOptions
    // if some options are supplied, here we overwrite defaults
    options = Object.assign( {} , defaultOptions, options)
    /*
    node.data - the associated data, as specified to the constructor
    node.depth - zero for the root node, and increasing by one for each descendant generation
    node.height - zero for leaf nodes, and the greatest distance from any descendant leaf for internal nodes
    node.parent - the parent node, or null for the root node
    node.children - an array of child nodes, if any; undefined for leaf nodes.
    node.value - the summed value of the node and its descendants; optional, set by node.sum.
    */

    // fetch the correct node from the DOM
    const svg = d3.select(host)
    // each little node circle
    const nodeRadius = "0.2%"
    // cache the DOM elements
    const elements = {}
    // some visual options

    // default <title>
    let title = "Human Biases"

    // config
    let 
        // dimensions +convert to number
        width = +svg.attr("width"),
        height = +svg.attr("height"),
        
        // middle of the diagram
        centerX = width / 2,
        centerY = height / 2,
        // radius of overall graph
        radius = width / 2,
        // tree radius
        dataRadius = radius * options.radiuses.data,
        // description distance from centre
        titleRadius = radius * options.radiuses.title,
        ringRadius = radius *  options.radiuses.ring,
        // how many degrees around do we draw on the tree
        coverage = 360 - options.mouth

    // JSON data
    let loadedData

        /*
        ,
        offset : {
            lines:240,
            circles:400,
            nodes:200
        }
        */

    // Create a <tspan> wrapped textbox using the plugin from
    // https://github.com/vijithassar/d3-textwrap
    // Thanks!
    const wrap = textwrap()
        // set fixed size for boxes from options
        .bounds({height: options.textBoxHeight, width: options.textBoxWidth})
        // wrap with tspans in all browsers
        .method('tspans')

    
    // Take an x,y coord and project them radially
    // Used by the traits' text
    const project = (x, distance) => {
        const angle = (x - 90) / 180 * pi
        return [distance * cos(angle), distance * sin(angle)]
    }

    // for radial data we want to give it a bit more room
    // https://github.com/d3/d3-hierarchy/blob/master/README.md#tree_separation
    const seperate = (a,b) => (a.parent == b.parent ? 1 : 2) / a.depth

    // Create our tree diagram (one trunk, plenty of branches)
    // using the ReingoldTilford tidy algorithm
    // https://github.com/d3/d3-hierarchy/blob/master/README.md#tree
    const branch = tree()
        // 360 degrees, radius
        .size([coverage, dataRadius])
        // how the nodes are distributed
        .separation(seperate)

        // options.duration.lines
        // options.duration.circles
        // options.duration.node

        // options.delay.lines
        // options.delay.circles
        // options.delay.node

    // for staggered animations
    const delayCircles = (d,i) => {
        // make sure that all of the lines have faded in already
        const pause = 0
        //i.length * options.duration.lines;
        // determine how many items are in this group...
        const quantity = d.parent ? d.parent.children.length : 1
        //console.log( quantity, i%quantity);
        return pause + (i%quantity * options.delay.circles)
    }

    const delayLines = (d,i) => i * options.delay.lines
    const delayNode = (d,i) =>  i * options.delay.nodes
        
    // Colour for items :)
    const colours = (item, index, completeData, optionalTotal=undefined) => {
        const percent = index / (optionalTotal || completeData.length)
        //const colour = hcl(255 *percent, 50, 20 )
        const colour = hsl( 355*percent, options.saturation, options.lightness )
        //console.log( "colour", { percent, index, length:completeData.length, colour} )
        return colour.toString()
    }

    // Curvy lines :)
    const lines = (d,i) => !d.parent ? "M0,0" : "M" + project(d.x, d.y)
                            + "C" + project(d.x, (d.y + d.parent.y) * 0.5)
                            + " " + project(d.parent.x, (d.y + d.parent.y) * 0.5)
                            + " " + project(d.parent.x, d.parent.y)

    // feed it a complete data file and then specify the key you want to prune to
    // note :I wrote this originally before ES6 was a thing so no Set
    const dataFetchFromKey = ( dataSet , key ) => {

        // loop through all children
        const traits = dataSet.children
        for ( let i=0,q=traits.length; i < q; ++i )
        {
            const branch = traits[i]
            if (key == branch.name)
            {
                // re-branch
                return {
                    "name": dataSet.name,
                    "children":[branch]
                }
            }
        }
        return loadedData
    }

    const setName = (name, colour='currentColor') => {
        // set some css too based on the size of name...
        const range = 16
        const scale = 32
        const length = name.length
        // the longer the word the smaller the font size
        const fontSize = length > range ? scale + range - length : scale
        
        // the longer the word, the shorter the letter spacing...
        const letterSpacing = length < range ? range - length : 0
        const multiWord = name.split( " " ).length
        const butt = !multiWord && length < 14
        // if the length is very short it looks weird away from the stub so 
        // justify the position differently
        elements.title
            .style( "font-size", `${fontSize}px` )
            .style( "letter-spacing", `${letterSpacing}px` )
            .style( "text-anchor", butt ? "middle" : "end" )
            .style( "fill", colour )
            .attr( "x", butt ? -180 : -20 )
            .text( name )
    }

// this get's tricky as there is an options.angle too
                     // FIXME:
        
    // Events ----------------------------------------------------------------------

    // Background has been clicked which is the equivalent
    // of reloading the page
    const onBackgroundClick = (item, index, completeData) => redraw( loadedData )

    // Click event
    const onClick = (item, index, completeData, clickEvent) => {
        let group, family
        switch (item.depth)
        {
            case 1:
                // console.log("Title",item.data.name, {item, index, completeData, event: clickEvent})
                group = "All"
                family = item
                break

            case 2:
                // console.log("Description",item.data.name, {item, index, completeData, event: clickEvent})
                group = "All"
                family = item.parent
                break

            // case 3:
            default:
                // console.log("Trait",item.data.name, {item, index, completeData, event: clickEvent})
                group = item.data.name
                family = item.parent.parent
        }

        // redraw...
        //console.error(item.depth, family.data.name, group, family );

        // filter descendants
        const trimmed = dataFetchFromKey(loadedData, family.data.name)
        // console.error(trimmed, loadedData, family.data.name)
        redraw( trimmed, clickEvent.depth )

        // copy to clipboard...?
        //selectAndCopyText();
    }


    const onEvent = (item, index, completeData) => {
        
        // d3.event is global!?!?!
        
        const hasChildren = item.hasOwnProperty("children")
        const hasParent = item.hasOwnProperty("parent")
        const isTrunk = hasChildren
        
        //
        if (isTrunk)
        {
             console.log("TRUNK!",event.type,event.path, {hasChildren, hasParent,event,item, index, completeData})
        }else{
            console.log("BRANCH!",event.type, event.path,{hasChildren, hasParent,event,item, index, completeData})
        }
        
        switch (event.type)
        {
            case "click":
                
                setName( item.data.name )
                // hmm TODO: isTrunk?
                onClick(item, index, completeData, event)
                break

            case "mouseover":
            case "focus":
               
                // update title...
                if (isTrunk)
                {
                    elements.heading.text( item.data.name )
                    // show quantity of biases
                    elements.subheading.text( item.children.length + '' +  item.data.name )
                    elements.accessibility.text( item.data.name )
                    onDescription({type:event.type, item})

                }else{

                    // outer nodes HAVE children, inner tree nodes do not
                    const fills = event.path.find(element => element && element.getAttribute ? element.getAttribute("fill") : null )
                    const col = fills ? fills.getAttribute("fill") : 'unset'                   

                    const parentData = item.parent
                    elements.heading.text( parentData.data.name )
                    elements.subheading.text( item.data.name )//.call(wrap)
                    elements.accessibility.text( item.data.name )

                    // set
                    setName(item.data.name, col)
                    onTrait({type:event.type, parentData,item})
                }
                break

            case "blur":
            case "mouseout":
                // reset titles
                elements.heading.text( loadedData.name )
                elements.subheading.text( loadedData.name )
                elements.accessibility.text( title )
                
                setName( title )
                break
        }
    }

    // FIXME:
    const redraw = ( data, depth ) => {

        // console.error( {data, loadedData} )

        // FIXME:
        // TODO:
        // check the depth of the data and if we are not at root level,
        // it is important to show the back button to allow the whole
        // Set the heading and the subheading to the sectional data...
        elements.heading.text( data.name )

        // remove existing elements
        elements.centredGroup.selectAll(".node").remove()

        // rotate descriptions?
        //descriptions.append( "text" ).attr( "transform", (d,i) => `rotate(${options.angle})` )
                
        draw(data, depth)
    }

    // Draw onto stage
    const draw = ( data, depth ) => {
        // 3. create our data & get the goodies out of there...
        
        // convert data into a hierachy with children and parents
        // NB. you can filter this here to only show certain data sets...
        const dataHierarchy = hierarchy( data )
        // create a root data node from the tree and our main heirachy...
        const dataTree = branch( dataHierarchy )
        // and create the heirachy that we will use to populate the data...
        const descendants = dataTree.descendants()

        // we can sniff out some data before we draw it for optimisations
        const depths = descendants.map( descendant => descendant.depth )
        const itemsAtDepth = descendants.filter( item => item.depth === depth )
        const quantityAtDepth = itemsAtDepth.length
        const quantityAtDepths = {}
        descendants.forEach( descendant => quantityAtDepths[descendant.depth] ? quantityAtDepths[descendant.depth]++ : quantityAtDepths[descendant.depth] = 1 )
        
        const quantityRatios = []
        for (let q in quantityAtDepths)
        {
            quantityRatios.push( quantityAtDepths[q] )
        }
        const depthCounters = [0,0,0,0]
        // console.error( quantityRatios, "DRAW",{ depths, quantityAtDepth, quantityAtDepths}, {itemsAtDepth, data, depth, hierarchy, dataTree, descendants} )
                    
        
        // 

        // now fetch our .node elements
        elements.dataPoints = elements.centredGroup.selectAll(".node")

        // the outside arcs / cirlce ...
        const ring = elements.centredGroup
            // .append("circle")
            // .attr( "class", "description-ring")
            // .attr( "r", ringRadius )
            // .attr( "fill", "transparent" )
            // .attr( "stroke", colours )0.94
            .append("g")  // If we ever figure out how to do multiple paths...
            //.attr( "stroke", colours )
                // .attr( "stroke-width", "8px" )
                .attr( "data-level", 3 )
                .attr( "class", "description-ring")
                
        // we walk up the descendants

        
        // draw ring paths (we use the data set to determine ar lengths)
        const total = quantityAtDepth


        const parts = new Array(total)
        const buffer = tau/36
        const arc = (( tau * coverage/360 ) + (buffer)) / total
        let a = -pi/2 - (buffer/2)
        for (let z=0; z<total; ++z)
        {   
            ring
                .append("path")
                .attr( "d", drawRingPath(ringRadius, a, arc) )
                .attr( "stroke", (item, index, completeData) => colours(item,z,undefined,total) )

            a += arc
        }    
              
        elements.ring = ring

        // Create data nodes on entered...
        const parents = {}
        const groups = elements.dataPoints
            .data( descendants )
            .enter()
                // draw our graphic nodes
                .append("g")
                // add class depending on type...
                .attr("class", (item, index, completeData) => "node" + (" node-"+item.depth) + (item.children ? " node--internal" : " node--leaf") )
                // link this into the parent... (it is always 3 to be fair!)
                //.attr( "data-level", 3 )
                .attr( "data-level", (item, index, completeData) => {
                    parents[item.data.name] = index
                    return item.depth 
                } )
                //.attr( "data-id", (item, index, completeData) => item.data.name )
                .attr( "data-tree", (item, index, completeData) => item.children ? index : parents[item.parent.data.name] )
                .on("click mouseover mouseout focus", onEvent)

        // put a group in the node group
        const nodes = groups
            .append("g")
            // move using coordinates set in the tree algo
            .attr( "transform", (item, index, completeData) => "translate(" + project(item.x, item.y) + ")" )
            .attr( "class", "endpoint")
            //.attr( "data-level", (item, index, completeData) => item.depth )

        elements.endpoints = nodes
        /*
        // draw and animate on data change
        dataPoints.update()
            .transition()
            .delay(function(d, i) {
                return i * 100;
            })
            .duration(1000);
        */

        // Lines -------------------------------------------------------------------
                  
        // You don't need to provide the y here yet but at some point
        // soon this will make the rotational matrix convert the coords
        // into a space relative to the screen
        const isUpsideDown = (x,y) => {
            return x < 90 || x > 270
        }

        // we want text upside down in the following quadrants
        // 0 | 0
        // 0 | 0
        const isTextUpsideDown = d => {
            //console.log("isTextUpsideDown", d.x, d.x < 180, (d.x + options.angle)%360 < 180) 
            // angles (d.x) 
            return (d.x + options.angle)%360 < 180
        }

        // Lines between the nodes
        const connections = groups.append("path")
            // draw path lines as data points
            .attr( "d", lines )
            // add class name
            .attr( "class", "connection")
            // draw the lines inbetween the nodes
            .attr( "stroke", (item,index,data) => {
                const total = quantityAtDepths[item.depth] - 1
                const s = depthCounters[ item.depth ]++
                return colours(item,s,data,total) 
            })
            // .attr( "stroke-dasharray", 1000 )
            // .attr( "stroke-dashoffset", 1000 )
            .attr( "data-level", 1 )
            // now animate in :);
            // .transition()
            // .duration( options.duration.lines )
            // .delay( delayLines )
            // .attr( "stroke-dashoffset", 0 )

        elements.connections = connections

        // Circles -----------------------------------------------------------------
        // Draw some overlaying data :)
        // circles on the node points
        elements.circles = nodes.append("circle")
            .attr( "class", (item,i) => `blob circle-${item.depth}` )
            // .attr( "r", 0)
            .attr( "r", (d,i) => d.children ? "1px" : nodeRadius )
            .attr( "data-level", (item,i) => item.depth )
            .attr( "fill", colours )
            .attr( "fill", (item,index,data) => {
                const total = quantityAtDepths[item.depth] - 1
                const s = depthCounters[ item.depth ]++
                return colours(item,s,data,total) 
            } )
            // transition in from tiny to regular
            // .transition()
            // .duration( options.duration.circles )
            // .delay( delayCircles )
            // .attr( "fill", colours )
            // // set radius depending on depth
            // .attr( "r", (d,i) => d.children ? "1px" : nodeRadius )

        
        // add some traits to the end of the lines :)
        const traits = nodes
            // ensure we are only drawing traits...
            .filter( (item, index, completeData) => !item.children )
            .append( "g" )
            .attr( "data-level", (item, index, completeData) => item.depth )
            // rotate if neccessary
            .attr( "transform", (d,i) => {
                // use the depth (i) to set the position
                // 180 + options.angle
                return "rotate(" + (isTextUpsideDown(d) ? d.x - 90 : d.x + 90) + ")"
                //return "translate(" + project(d.x, d.y) + ") rotate(" + (d.x < 180 ? d.x - 90 : d.x + 90) + ")"
            })

        // hit area
        traits.append("rect")
            .attr( "x", (d,i) => d.x < 180 ? 0 : -100 )
            .attr( "y", -4)
            .attr( "fill", "transparent")
            // FIXME: constant here shuldnt b
            .attr( "width", 140 )
            .attr( "height", 8 )

        // rotated text trait
        traits.append( "text")
            .attr( "class", "trait")
            // fill with the correct colour for this item
            .attr( "fill", colours )
            // try and lower text
            .attr( "dy", "0.33em" )
            // set the coordinate for the text element
            // .attr( "y", "-3rem" )
            .attr( "x", (d,i) => isTextUpsideDown(d) === !d.children ? 6 : -6 )
            // // justify left or right depending on how rotated everything is
            .style("text-anchor", (d,i) => isTextUpsideDown(d) === !d.children? "start" : "end" )
            // .attr( "transform", (d,i) => {
            //     // use the depth (i) to set the position
            //     // 180 + options.angle
            //     return "rotate(" + (isTextUpsideDown(d) ? d.x - 90 : d.x + 90) + ")"
            //     //return "translate(" + project(d.x, d.y) + ") rotate(" + (d.x < 180 ? d.x - 90 : d.x + 90) + ")"
            // }) // set the text from the name of the JSON node
            .text((t,i) => t.data.name )

        elements.traits = traits
    
        // Descriptions ------------------------------------------------------------

        // add descriptions to the traits...
        const descriptions = nodes
            // descriptions are the first data sets that describe their children
            .filter( (item, index, completeData) => item.depth === 2 )
            .append( "g")
            .attr( "class", "description")
            .attr( "data-level", (item, index, completeData) => item.depth )
            .attr( "fill", colours )
            // set the coordinate for the text element
            // .attr( "x", function(d,i) {
            //     return 0;
            //     //return d.x < 180 === !d.children ? 6 : -6;
            // })
            // rotate if neccessary
            // use the depth (i) to set the position
            .attr( "transform", (d,i) => "translate(" + project(d.x, titleRadius + options.bubbleRadius) + ")" )

        // Funky blobs on the rim
        descriptions.append( "circle" )
            .attr( "class", "description-blob")
            // .attr( "cy", "-12px")
            .attr( "r", "12px" )
            .attr( "fill", colours )
            .attr( "stroke-width", "6px" )

        // and their associated descriptions
        //.append( "g")
        elements.descriptions = descriptions.append( "text" )
            // centralise y approx
            // .attr("dy", function(d,i) {
            //     var upsideDown = d.x < 90 || d.x > 270;
            //     return !upsideDown ? "56px" : "-56px";
            // })
            // .attr( "x", options.textBoxWidth * 0.5 )//"4" )
            .attr( "x", options.bubbleRadius + 4 )//"4" )
            .attr( "y", "18" )
            //.attr("y", (d,i) => isTextUpsideDown(d) ? -26 : 26 )
            // .attr("y", (d,i) => isUpsideDown(d.x, d.y) ? -26 : 26 )
            //.attr("dx", options.textBoxWidth * 0.5)
            // rotate to face normal of the line
            // .attr( "transform", (d,i) => "translateX(" + -options.textBoxWidth/2 + "px)" )
            //.attr( "transform", (d,i) => "rotate(" + (!isUpsideDown(d.x, d.y) ? d.x - 180 : d.x ) + ")" )// 0 0/2
            //.attr( "transform", (d,i) => `rotate(${ d.x + 90 }) translate(${-options.textBoxWidth/2})` )

            // 12 here is radius of blob translate(${isUpsideDown(d.x) ? -( options.textBoxWidth/2) :  ( options.textBoxWidth/2) })
            .attr( "transform", (d,i) => `rotate( ${ d.x + 174})` )
            // .attr( "transform", (d,i) => `rotate( ${ d.x + 177 }) translate(12)` )
           
            // .attr( "transform", (d,i) =>   `rotate(${ !isUpsideDown(d.x) ? d.x + 3 : d.x + 183 }) 
            // .attr( "transform", (d,i) =>   `rotate(${ !isUpsideDown(d.x) ? d.x + 3 : d.x + 183 }) 
            //                                 translate(${isUpsideDown(d.x) ? -( options.textBoxWidth/2) :  ( options.textBoxWidth/2) })` )
            // // justify left or right depending on how rotated everything is
            .style( "text-anchor", (d,i) => {
                 return "start"
               // return "middle"
                //return d.x < 180 ? "start" : "end";
            })
            // set the text from the name of the JSON node
            .text( (t,i) => t.data.name )
            .call(wrap)

        // why is this rotating around???
       // elements.descriptions.attr( "transform", (d,i) => `rotate( ${ d.x + 177 })` )
        // text wrap but preserve positions!
            
        elements.descriptionButtons = descriptions

        // descriptions
        // const ringPaths = elements.nodes
        //                     // descriptions are the first data sets that describe their children
        //                     .filter( (item, index, completeData) => item.depth === 2 )
        //                     .append("path")
        //                         .attr( "d", drawRingPath(ringRadius, -/2,  * coverage/360 ) )
        //                         .attr( "class", "description-ring-path")
        //                         //.attr( "transform", (d,i) => "translate(" + project(d.x, titleRadius + options.bubbleRadius) + ")" )
                            
    
        /*

        // now create our outer labels
        var sections = centredGroup
            .selectAll(".division")
            // populate with all children of parent node
            .data( subdivisions )
            // grab none-existant DOM items and continue
            .enter()
                // add a path to this node
                .append("text")
                .attr("class", "legend")
                .attr("transform", function(d,i) {
                    // move depending on stuff
                    return "rotate(" + (d.x < 180 ? d.x - 90 : d.x + 90) + ")";
                })
                .text( function(t,i) {
                    var text = options.showNumbers ? (i+1) + ". " : "";
                    return text + t.data.name;
                });

                */
        // now create our outer labels
        const legend = elements.centredGroup
            .selectAll(".legend")
            // populate with all children of parent node
            .data( descendants )
            // descriptions are the first data sets that describe their children
            .filter( (item, index, completeData) => item.depth === 1 )
            // grab none-existant DOM items and continue
            .enter()
                // add a path to this node
                .append("text")
                .attr("class", "legend")
                // move depending on stuff
                .attr( "transform", (d,i) => "translate(" + project(d.x, titleRadius) + ")" )
                .text( function(t,i) {
                    const text = options.showNumbers ? (i+1) + ". " : ""
                    return text + t.data.name
                })

        // Data node has been removed
        const exit = elements.dataPoints
            .exit()
                .transition()
                .delay( delayNode )
                .duration( options.duration.nodes )
                .remove()

        return elements
    }

    // External force that allows for the entire thing to be rotated
    const revolve = angle => {

        // rotate the main thing...
        // NB. SVG is UNITLESS so % won't work here
        elements.centredGroup.attr( "transform", `rotate(${options.angle}) translate(${centerX}, ${centerY})`)
        
        // 
        // elements.descriptions.attr( "transform", (d,i) => `rotate(${ d.x + options.angle + 90 }) translate(${-options.textBoxWidth/2})` )

        // TODO: also the main title flip vertically?
        // elements.title

        // save into option
        options.angle = angle
    }

    // Draw shared base elements
    const render = data => {

        // Set viewbox to the whole size of the infographic
        svg.attr("viewBox", "0 0 "+width+" "+height )

        // 1. Add a title node to our root SVG node for accessibility
        title = data.name
        elements.accessibility = svg.append("title").text( title )
        
        // 2. draw a bounding box
        elements.background = svg
            .append("circle")
                .attr( "cx", width/2 )
                .attr( "cy", height/2 )
                .attr( "class", "wallpaper")
                .attr( "r", width/2 )
                .attr( "fill", "transparent" )
                .on("click", onBackgroundClick)
            // .append("rect")
            //     .attr( "x", 0)
            //     .attr( "y", 0)
            //     .attr( "class", "wallpaper")
            //     .attr( "width", width )
            //     .attr( "height",height )
            //     .attr( "fill", "transparent" )
            //     .on("click", onBackgroundClick)

        // 4. Add a large visual title
        // the sub dvisions and descriptions are at
        // data.children[0].children[0].name;
        elements.heading = svg
            .append("text")
                // set the coordinate for the text element
                //.attr( "x", centerX-(dataRadius/2) )
                // the 6 here is just to align it with the outer size of the circles
                .attr( "x", options.padding )
                .attr( "y", options.padding )
                .attr( "class", "heading")
                .style( "text-anchor", "start" )
                // animate :)
                //.transition()
                //.duration(2000)
                // set the text from the name of the JSON node .toUpperCase()
                .text( data.name )

        elements.subheading = svg
            .append("text")
                // set the coordinate for the text element
                //.attr( "x", centerX-(dataRadius/2) )
                // the 6 here is just to align it with the outer size of the circles
                .attr( "x", options.padding )
                .attr( "y", options.padding + 12 )
                .attr( "class", "subheading")
                .style( "text-anchor", "start" )
                // animate :)
                //.transition()
                //.duration(2000)
                // set the text from the name of the JSON node .toUpperCase()
                .text( data.name )

        // 7. Create a centralised group to hold our visualiser rotate(45deg)
        elements.centredGroup = svg
            .append("g")
                // NB. SVG is UNITLESS so % won't work here
                .attr( "transform", `rotate(${options.angle}) translate(${centerX}, ${centerY})`)
                .attr( "class", "visualiser")

        // NB. This is animated asa to set the text in cols we need it to be unrotate!!

        
        // Finally. Add a large visual title inside the visualiser
        // the sub dvisions and descriptions are at
        // data.children[0].children[0].name;
        elements.title = elements.centredGroup
            .append("text")
                .attr( "class", "title")
                // set the coordinate for the text element
                //.attr( "x", centerX-(dataRadius/2) )
                // the 6 here is just to align it with the outer size of the circles
                // -(dataRadius+2) 
                .attr( "x", -180 )
                .attr( "dy", "0.21em" )
                // angle!
                .attr( "transform", (d,i) => `rotate(${ -(((360 - coverage)/2) + 270) })` )    
                // return `translate(${centerX-gap} , ${centerY}) rotate(${mid})`
                // left justify
                //.style( "text-anchor", "end" )
                // animate :)
                //.transition()
                //.duration(2000)
                // set the text from the name of the JSON node .toUpperCase()
                .text( data.name )

        // draw this data set
        loadedData = data

        // original depth is level 3
        return draw( data, level )
    }

    // console.log("Loading data from ", dataFile)

    // if the data file is an object...

    // now load in our data and project it radially :)
    const loading = json( dataFile )
        .then( data => render(data) )
        .catch( error => {
            //console.error("Failed to load JSON", dataFile, error)
            throw Error(error)
        } )

    // resize monitor?...
    // window.addEventListener('resize', resize, true);
    // resize();

    return {
        loadPromise:loading,
        setTraitCallback: cb => onTrait = cb,
        setDescriptionCallback: cb => onDescription = cb,
        revolve:revolve
    }
}

// now to put this in the global scopes
window.BIASES = bias
export const registerBiases = bias